.CmtSubUpdate {
    padding-top: 0.5rem;
    .ButtonRipple {
        width: 100%;
    }
}

.CmtSubUpdate_body {
    max-height: calc(100vh - 9rem);
    padding: 0.5rem;
    overflow-y: auto;
}

.CmtSubUpdate_div-textarea {
    padding: 0.25rem;
    border: 1px solid var(--md-bg-ccc);
}
// .
.CmtSubUpdate__textarea {
    max-height: 5rem;
}

.CmtSubUpdate_pic {
    margin: 0.5rem 0;
}
// .
.CmtSubUpdate_pic-contain {
    position: relative;
}
// ..
.CmtSubUpdate_pic-item {
    padding: 0.25rem;
    border: 1px solid var(--md-bg-ccc);

    .ImgVidPreviewItem {
        display: flex;
        align-items: center;
        justify-content: center;

        position: relative;
        
        width: 10rem;
        max-width: 100%;
        height: 10rem;
    }
    img,
    video {
        width: auto;
        min-width: 50%;
        max-width: 100%;
        height: auto;
        max-height: 100%;
    }
}
// ..
.CmtSubUpdate_pic-none {
    position: absolute;
    top: 50%;
    left: 50%;

    width: 100%;
    transform: translate(-50%, -50%);

    text-align: center;
    color: var(--md-bg-ccc);
    font-size: 18px;
    font-weight: bold;
}

.CmtSubUpdate_file {
    margin: 0.25rem;
}
// .
.CmtSubUpdate_file-row {
    display: flex;
    justify-content: flex-end;
}
